import React, { useState } from 'react'
import './comp-styles.css'


export default function ProtectedEntry({id,task}) {

    const [state,setState] = useState({pDisp:'block',iDisp:'none'})
    const [input, setInput] = useState("");


    
    
    return (
        <div className="entry">
            <p style={{display:state.pDisp}}>{(id+1).toString()+") "+task}</p>
        </div>
    )
}
