import logo from "./logo.svg";
import "./App.css";

import React, { useEffect } from "react";
import { useState } from "react";
import { BrowserRouter, Switch, Route, Redirect, Link } from "react-router-dom";
import Admin from "./Components/Admin";
import User from "./Components/User";

export default function App() {
  const users = [
    { name: "prem", password: "pass" },
    { name: "siva", password: "hellow" },
    { name: "sakthi", password: "sword" },
  ];
  const admins = [
    { name: "admin", password: "adminpass" },
    { name: "adam", password: "eve" },
    { name: "eve", password: "adam" },
  ];
  const [name, setName] = useState("");
  const [pass, setPass] = useState("");
  const [warn, setWarn] = useState("none");

  const [isValid, setisValid] = useState(false);
  const [isUser, setisUser] = useState(false);

  function auth() {
    for (var i of admins) {
      if (i.name == name && i.password == pass) {
        console.log("Admin user");
        setWarn("none");
        setisUser(false);
        setisValid(true);
      }
    }
    for (var i of users) {
      if (i.name === name && pass === i.password) {
        console.log("Normal user");
        setWarn("none");
        setisUser(true);
        setisValid(true);
      }
    }
    setWarn("block");
  }

  let inpName = (e) => {
    setName(e.target.value);
  };
  let inpPass = (e) => {
    setPass(e.target.value);
  };

  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/">
          {isValid ? (
            <Redirect to="/app" />
          ) : (
            <div className="card">
              <input onChange={inpName} type="text" placeholder="User name" />
              <input
                onChange={inpPass}
                type="password"
                placeholder="Password"
              />
              <Link to="/app">
                <button onClick={() => auth()}>Login</button>
              </Link>
              <p style={{ display: warn, color: "tomato" }}>
                Please check the password
              </p>
            </div>
          )}
        </Route>
        <Route exact path="/app">
          {isValid ? (
            isUser ? (
              //<Redirect to="/user" />
              <User appdata={users} />
            ) : (
              // <Redirect to="/admin" />
              <Admin appdata={admins} />
            )
          ) : (
            <Redirect to="/" />
          )}
        </Route>
      </Switch>
    </BrowserRouter>
  );
}
